import { Component, OnInit } from '@angular/core';
import { Employee } from './employee';
import { EmployeesService } from './employee.service'
import {Router} from "@angular/router";
@Component({
  selector: 'app-employees',
  templateUrl: './employees.component.html',
  styleUrls: ['./employees.component.css']
})
export class EmployeesComponent implements OnInit {

  employees: Employee[];
  getEmployeesSub: any;
  loadingError: boolean;
  filteredEmployees: Employee[];

  constructor(private eService: EmployeesService,
              private router: Router) { this.loadingError = false;}

  ngOnInit() {
    this.getEmployeesSub = this.eService.getEmployees().subscribe( (employees)=>{
      this.employees = employees;
      this.filteredEmployees = employees;

    },()=>{
      this.loadingError = true;
    })
  }
  routeEmployee(id: string){
    this.router.navigate(['/employee', id]);
  }
  onEmployeeSearchKeyUp(event: any){
    let substring : string = event.target.value.toLowerCase();
    this.filteredEmployees = this.employees.filter((e) => ((e.FirstName.toLowerCase().indexOf(substring) !== -1 ) || (e.LastName.toLowerCase().indexOf(substring) !== -1)))
  }

  ngOnDestroy(){
    this.getEmployeesSub.unsubscribe();
  }



}



