import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Position} from "./position";

@Injectable()
export class PositionService {

  private url = 'https://web344app.herokuapp.com/positions';
  private url2 = 'https://web344app.herokuapp.com/positions/';

  constructor(private http : HttpClient) { }

  getPositions():Observable<Position[]>{
    return this.http.get<Position[]>(this.url);
  }

  savePosition(position: Position){
    return this.http.put<any>(this.url2 + position._id, position);
  }

  getPosition(id: string):Observable<Position>{
    return this.http.get<Position>(this.url2 + id);
  }

}



