import {Component, OnDestroy, OnInit} from '@angular/core';
import {EmployeeRaw} from "./employee-raw";
import {ActivatedRoute} from "@angular/router";
import {EmployeesService} from "./employee.service";
import {PositionService} from "./position.service";
import {Position} from "./position";

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})
export class EmployeeComponent implements OnInit, OnDestroy {


  paramSubScription: any;
  employeeSubscription: any;
  getPositionsSubscription: any;
  saveEmployeeSubscription: any;
  employee: EmployeeRaw;
  positions: Position[];
  successMessage = false;
  failMessage = false;

  constructor(private route: ActivatedRoute,
              private employeeService: EmployeesService,
              private positionService: PositionService) {

  }

  ngOnInit() {
    this.paramSubScription = this.route.params.subscribe((params) => {
      this.employeeSubscription = this.employeeService.getEmployee(params['_id']).subscribe((emp) => {
        this.employee = emp[0];

        this.getPositionsSubscription = this.positionService.getPositions().subscribe( data => {
          this.positions = data;
        });
      });
    });
  }


  onSubmit(){
    this.saveEmployeeSubscription = this.employeeService.saveEmployees(this.employee).subscribe(()=>{
      this.successMessage = true;
      setTimeout(()=>{
        this.successMessage = false;
      }, 2500)
    },
    ()=>{
      this.failMessage = true;
      setTimeout(()=>{
        this.failMessage = false;
      }, 2500)
    })
  }

  ngOnDestroy(){
    if(this.paramSubScription){
      this.paramSubScription.unsubscribe();
    }

    if(this.employeeSubscription){
      this.employeeSubscription.unsubscribe();
    }

    if(this.getPositionsSubscription){
      this.getPositionsSubscription.unsubscribe();
    }

    if(this.saveEmployeeSubscription){
      this.saveEmployeeSubscription.unsubscribe();
    }
  }

}



