import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Employee } from './employee';
import {EmployeeRaw} from "./employee-raw";


@Injectable()
export class EmployeesService {
  private url = 'https://web344app.herokuapp.com/employees';
  private url2 = 'https://web344app.herokuapp.com/employee-raw/'
  private url3 = 'https://web344app.herokuapp.com/employee/';

  constructor(private http: HttpClient) { }

  getEmployees(): Observable<Employee[]>{
    return this.http.get<Employee[]>(this.url);
  }

  saveEmployees(employee: EmployeeRaw){
    return this.http.put<any>(this.url3 +  employee._id,employee );
  }

  getEmployee(id:string) : Observable<EmployeeRaw[]>{
    return this.http.get<EmployeeRaw[]>(this.url2+ id);
  }

}



